import React, { useContext } from 'react'
import styled from 'styled-components'

import UXContext from 'utils/UXContext'
import MagicLink from 'components/base/MagicLink'

const Text = styled.p``
const Title = styled.h3``
const StyledLink = styled.button`
  display: inline;
  margin: 0;
  padding: 0;
  color: ${(props) => props.theme.colors.main};
  text-decoration: underline;
  background: transparent;
  border: none;
  cursor: pointer;
`
export default function ContactPrompt(props) {
  const { toggleEmbedOpen } = useContext(UXContext)

  return false ? (
    <>
      {!props.configurator && !props.contact && (
        <>
          <Title>
            Comment intgrer ces donnes  mon site ou application ?
          </Title>
          <Text>
            <strong>Vous souhaitez afficher Recosant sur votre site ?</strong>
            <br />
            Personnalisez le et intgrez le facilement grace {' '}
            <StyledLink
              onClick={() => {
                toggleEmbedOpen()
              }}
            >
              notre configurateur
            </StyledLink>
            .
          </Text>
        </>
      )}
      <Text>
        <strong>
          Vous souhaitez rutiliser les donnes brutes ou obtenir de l'aide pour
          intgrer Recosant ?
        </strong>
        <br />
        Contactez nous {' '}
        <MagicLink to='mailto:contact@recosante.beta.gouv.fr'>
          contact@recosante.beta.gouv.fr
        </MagicLink>{' '}
        pour bnficier de notre expertise et accompagnement.
      </Text>
      {!props.contact && (
        <Text>
          <strong>Vous souhaitez rutiliser le code de Recosant ?</strong>
          <br />
          Recosant est dvelopp de manire ouverte (open source). Lensemble
          du code est{' '}
          <MagicLink to={process.env.GATSBY_REPOSITORY_URL}>
            disponible librement
          </MagicLink>
          .
        </Text>
      )}
    </>
  ) : null
}
